@-webkit-keyframes tooltipanimation {
  from {
    margin-top: -28px;
  }
  to {
    margin-top: 0;
  }
}
@keyframes tooltipanimation {
  from {
    margin-top: -28px;
  }
  to {
    margin-top: 0;
  }
}
.direction {
  width: 200px;
  position: fixed;
  top: 120px;
  left: 20px;
  font-size: 14px;
  line-height: 1.2;
  text-align: center;
  background: #9365b8;
  padding: 10px;
  color: #fff;
}
@media (max-width: 480px) {
  .direction {
    position: static;
  }
}
.input-row {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.input-group {
  flex: 1 1 calc(33.333% - 15px);
  box-sizing: border-box;
}

.form-control1 {
  width: 100%;
}
.delivery-zone-select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
  font-size: 16px;
}

.delivery-zone-select::-ms-expand {
  display: none;
}

.delivery-zone-select {
  appearance: none;
}

.input-group::after {
  content: "\f078";
  font-family: "FontAwesome";
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
}
@media only screen and (max-width: 768px) {
  .input-row .registerinput {
    width: 100% !important;
    margin-bottom: 15px;
  }
  .input-row {
    display: block;
    width: 100%;
  }

  .button1 {
    width: 100%;
  }

  .signup-wrapper1 {
    margin-top: 20px;
  }
}

@media only screen and (max-width: 480px) {
  .inner-wrapper1 {
    width: 100%;
  }

  .title {
    font-size: 1.2rem;
  }

  .button1 {
    width: 90%;
    font-size: 1rem;
  }
}
